const uni = require('uni').default;





























import pageHead from "../../components/page-head.vue"
export default {
	data(){
		return{
			title:'视频',
			autoplay:true,
			src:'http://img.cdn.qiniu.dcloud.net.cn/wap2appvsnative.mp4',
			videoContext:'',
			inputValue: '',
			danmuList: [{
					text: '第 1s 出现的弹幕',
					color: '#ff0000',
					time: 1
				},
				{
					text: '第 3s 出现的弹幕',
					color: '#ff00ff',
					time: 3
				}
			]
		}
	},
	onReady:function(res){
		this.videoContext = uni.createVideoContext('myvideo');
	},
	components:{
		pageHead
	},
	methods:{
		videoErrorCallback:function(e){
			 console.log('视频错误信息:' + e.target.errMsg);
		},
		bindButtonTap:function () {
			uni.chooseVideo({
				sourceType:['album','camera'],
				maxDuration:60,
				camera:['front','back'],
				success:function(res){
					this.src = res.tempFilePath;
					console.log('视频选择完成,路径为'+res.tempFilePath)
					console.log('当前视频播放源路径为'+this.src)
				}
			})
		},
		bindInputBlur:function (e) {
			this.inputValue = e.target.value
			// console.log(this.inputValue)
		},
		bindSendDanmu:function () {
			var self = this;
			self.videoContext.sendDanmu({
				text:self.inputValue,
				color:self.getRandomColor()
			});
		},
		getRandomColor:function () {
			const rgb = []
			for (let i =0;i<3;i++) {
				let color = Math.floor(Math.random()*256).toString(16)
				color = color.length == 1? '0'+color:color
				rgb.push(color)
			}
			return '#'+rgb.join('');
		}
	},
}



